import React, { Component } from "react";
import { Menu } from "semantic-ui-react";

import InterestingProperties from "./InterestingProperties/InterestingProperties";
import AllProperties from "./AllProperties/AllProperties";

export default class PropertiesPanel extends Component {
  state = {
    activeItem: "интересные"
  };
  clickMenu = (e, { name }) => {
    this.setState({ activeItem: name });
  };
  render() {
    const { activeItem } = this.state;
    return (
      <div>
        <Menu secondary size="tiny">
          <Menu.Item
            onClick={this.clickMenu}
            active={activeItem === "интересные"}
            name="интересные"
          />
          <Menu.Item
            onClick={this.clickMenu}
            active={activeItem === "все"}
            name="все"
          />
        </Menu>
        {activeItem === "интересные" ? (
          <InterestingProperties />
        ) : (
          <AllProperties />
        )}
      </div>
    );
  }
}
