import React, {useState} from "react";
import {Checkbox, Dimmer, Input, Loader, Radio, Segment} from "semantic-ui-react";

import UserAvatar from "./UserAvatar/User";
import {connect} from "react-redux";
import './style.less'
import NoContent from "../utils/NoContent";
import {selectEntities} from "../../store/entitiesReducer";
import {getObj} from "../../store/utils";
import SearchIcon from "../utils/SearchIcon";


const EntitiesPanel = (props) => {
    const noContent = <NoContent text={'Тут будут пользователи, группы, посты или прочая медиа информация'}
                                 popup={'Введите запрос в поисковую строку сверху'}/>;

    const toggleItemSelection = (itemID) => {
        if (props.selectedEntities.includes(itemID))
            props.selectEntities(props.selectedEntities.filter(item => item !== itemID));
        else
            props.selectEntities([...props.selectedEntities, itemID])
    };
    const [isNotValidHidden, changeNotValidHiddenMode] = useState(true);
    const [searchValue, setSearchValue] = useState('');

    const isEntityVisible = (entity) => {
        const fitToSearch = (search, value) => {
            if (!search)
                return true;
            if (!value)
                return false;
            return String(value).toLowerCase().includes(search.toLowerCase());
        };
        return (!isNotValidHidden || entity.valid) && (
            fitToSearch(searchValue, entity.name) ||
            fitToSearch(searchValue, entity.username) ||
            fitToSearch(searchValue, entity.nativeID)
        );
    };
    const visibleEntities = props.entities.filter(item => isEntityVisible(item));

    return (
        <Dimmer.Dimmable blurring as={Segment} clearing dimmed={false}>
            {
                props.entities.length > 0 &&
                <div style={{display: 'flex', alignItems: 'center', marginBottom: '1rem', marginLeft: '1rem'}}>
                    <Input value={searchValue}
                           onChange={(e, data) => setSearchValue(data.value)}
                           size={'mini'}
                           style={{marginRight: '1rem'}}
                           icon={<SearchIcon isInputStringEmpty={!searchValue.length}
                                             onClose={() => setSearchValue('')}/>}/>
                    <div style={{marginRight: '0.5rem'}}>{'Выделить'}</div>
                    <Checkbox
                        indeterminate={
                            props.selectedEntities.length > 0 && (visibleEntities.length !== props.selectedEntities.length)
                        }
                        checked={visibleEntities.length === props.selectedEntities.length}
                        onChange={(e, data) => {
                            const selected = data.checked;
                            if (selected)
                                props.selectEntities(visibleEntities.map(item => item.id));
                            else
                                props.selectEntities([]);
                        }}
                    />

                    <div style={{display: 'flex', marginLeft: 'auto', alignItems: 'center'}}>
                        <div style={{marginRight: '0.5rem'}}>Скрыть невалид</div>
                        <Checkbox onClick={((e, data) => changeNotValidHiddenMode(data.checked))}
                                  checked={isNotValidHidden} style={{marginRight: '1rem'}}/>
                        <div style={{marginRight: '1rem'}}>Список</div>
                        <Radio toggle/>
                        <div style={{marginLeft: '1rem', marginRight: '0.5rem'}}>Граф</div>
                    </div>
                </div>
            }

            <div className="entitiesContainer" style={{overflow: 'auto', maxHeight: '65vh'}}>

                <Dimmer inverted active={props.isLoading}>
                    <Loader style={{top: '150px'}} active/>
                </Dimmer>
                {!props.entities.length && noContent}
                {props.entities.filter(obj => isEntityVisible(obj)).map((obj) => (
                    <UserAvatar
                        entity={obj}
                        key={obj.id}
                        selected={props.selectedEntities.includes(obj.id)}
                        toggleSelection={() => toggleItemSelection(obj.id)}
                    />
                ))}
            </div>
        </Dimmer.Dimmable>
    )

};

const mapStateToProps = (state) => {
    const entitiesIDS = state.workSpace.entities.entities;
    const entities = entitiesIDS.map(id => getObj(state, 'entities', id));
    return {
        entitiesIDS: entitiesIDS,
        entities: entities,
        isLoading: state.workSpace.entities.isLoading,
        selectedEntities: state.workSpace.entities.selectedEntities
    }
};

const mapDispatchToProps = {
    selectEntities
};

export default connect(mapStateToProps, mapDispatchToProps)(EntitiesPanel);
