import React, {useState} from "react";
import {Accordion, Icon, Input, Table} from "semantic-ui-react";
import {connect} from "react-redux";
import SearchIcon from "../../../utils/SearchIcon";

const AllProperties = (props) => {
    const [searchValue, setInputValue] = useState('');
    const [activeID, setActiveID] = useState(-1);
    const [properties, setProperties] = useState(props.allProperties);

    const toggleProperty = (e, titleProps) => {
        const itemId = titleProps.index;

        const newActiveId = activeID === itemId ? -1 : itemId;
        setActiveID(newActiveId);
    };

    const searchStringChange = (value) => {
        const newProperties = [];
        for (let property of properties) {
            const {name} = property;
            if (name.toLowerCase().indexOf(value.toLowerCase()) !== -1) {
                newProperties.push(property);
            }
        }
        setInputValue(value);
        if (!value)
            setProperties(props.allProperties);
        else {
            setProperties(newProperties);
            if (newProperties.length)
                setActiveID(newProperties[0].id);
        }
    };
    return (
        <>
            <Input fluid
                   icon={<SearchIcon isInputStringEmpty={!searchValue.length} onClose={() => searchStringChange('')}/>}
                   value={searchValue}
                   placeholder={'Грамотность, 12, школа, общественные статус'}
                   onChange={(e, data) => {
                       searchStringChange(data.value)
                   }}
            />
            <Accordion>
                {properties.map((property) => (
                    <Accordion.Accordion key={property.id}>
                        <Accordion.Title
                            index={property.id}
                            active={activeID === property.id}
                            onClick={toggleProperty}
                        >
                            <Icon name="dropdown"/>
                            {props.activePropertyId === property.id ? <b>{property.name}</b> : property.name}

                            <Icon
                                link
                                circular
                                style={{float: "right"}}
                                name="chart line"
                            />
                        </Accordion.Title>
                        <Accordion.Content active={activeID === property.id}>
                            <Table selectable>
                                <Table.Body>
                                    {property.values.map(subProperty => (
                                        <Table.Row
                                            key={subProperty.name}>
                                            <Table.Cell width="10">{subProperty.name}</Table.Cell>
                                            <Table.Cell width="5">{subProperty.value}</Table.Cell>
                                            <Table.Cell width="2">
                                                <Icon link name="eye" color="grey"/>
                                            </Table.Cell>
                                        </Table.Row>
                                    ))}
                                </Table.Body>
                            </Table>
                        </Accordion.Content>
                    </Accordion.Accordion>
                ))}
            </Accordion>
        </>
    );
}

const mapStateToProps = (state) => ({
    allProperties: state.workSpace.controls.properties.all,
});

export default connect(mapStateToProps)(AllProperties)
